import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the LifestyleCheckPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lifestyle-check',
  templateUrl: 'lifestyle-check.html',
})
export class LifestyleCheckPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LifestyleCheckPage');
    setTimeout(() => {
      this.navCtrl.push("MstoreSnkPage");
    }, 5000);
  }

}



